.banner_button {
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-size: 1.2rem;
  font-weight: 700;
  border-radius: 0.2vw;
  padding: 1rem 3rem;
  margin-right: 2rem;
  background-color: rgba(51, 51, 51, 0.5);
}

.play {
  color: #000;
  background-color: #fff;
}



.banner_button:hover {
  color: #000;
  background-color: #c6c6c6;
  transition: all 0.2s;
}


.banner-fadeBottom{
    height: 40vh;
   background-image: linear-gradient(
    180deg,transparent,rgb(37,37,37,0.91),#111
   );
}




.banner .pairing-button {
  font-size: 0.3rem;
}